.LoggedinSide {
  position: fixed;
  left: 0;
  height: 100vh;
  max-width: 1326px;
}

.display-img {
  display: inline-block;
}

.navBelowHead {
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}

.box-rect {
  border-radius: 6px;
  box-shadow: 0px 2px 6px 0 rgba(0, 0, 0, 0.15);
  background-color: #ffffff;
  display: flex;
  flex-wrap: wrap;
}

.DashMarg {
  font-size: 24px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 0.92;
  letter-spacing: normal;
  text-align: left;
  color: #333333;
}

.boxHead {
  font-size: 13px;
  line-height: 1.69;
  margin-bottom: 0;
  text-align: left;
  color: #666666;
}

.numberColor {
  font-size: 24px;
  line-height: 0.92;
  text-align: left;
  color: #fa6e31;
}
